@import url('https://fonts.googleapis.com/css2?family=Staatliches&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Play&display=swap');
.signInButton{
    background-color: tomato;
    min-width: 150px;
    min-height: 40px;
    border-radius: 4px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin:5px 20px;
}
.signInButton>p{
    padding: 0;
    margin: 0;
}
.logo{
    display: inline-flex;
    margin:5px 20px;
}
.createComment{
    background-color: #eddcd9;
    border-radius: 2px;
    margin: 2vw;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 3vw;
}
.createComment>textarea{
    border: none;
    background-color: #eddcd9;
}
#comment{
    display: flex;
    padding: 3px;
    margin: 3vw;
}
#commentOwner{
    display: flex;
}
#commentOwner>img{
    width: 5vw;
    height: 5vw;
    border-radius: 50%;
}
.commentBody>p{
    color: #ed7966;
    margin: 0;
    padding: 0;
    text-align: left;
    padding-left: 2vw;
    font-family: 'Staatliches', cursive,'Courier New', Courier, monospace;
}
#commentText{
    font-family: 'Play', sans-serif;
    font-size: small;
    color: black;
}
.logo{
    display: flex;
    align-items: center;
}
.logo>h3{
    font-size: xx-large;
    margin: 0;
    color: #ed7966;
}
.logo>p{
    font-family: 'Play', sans-serif;
}