@import url('https://fonts.googleapis.com/css2?family=Staatliches&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');




.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
}
.createPostLogin{
    display: flex;
    align-content: center;
    justify-content: center;
    padding: 20px;
}
.createPostUpload>textarea{
    border: none;
}
.createPostUpload>textarea:focus{
    outline: none;
}
.createPostUpload>p{
    font-family: 'Staatliches', cursive,'Courier New', Courier, monospace;
    font-size: large;
    color: rgb(238, 8, 8);
    margin: 0;
}
.createPost{
    display: flex;
    justify-content: center;
}
#uploadedImg{
    display: none;
    width: 20vw;
}
#btns{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
#upload{
    background-color: #ed7966;
    border: none;
    border-radius: 4px;
    color: white;
    padding: 1vw;
}

.createPostUpload{
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: center;
    padding: 20px;
    width: 54vw;
    border: 2px solid black;
    margin: 2vw;
    border-radius: 4px;
    background-color: white;
}
.createPostUpload>p{
    display: flex;
}
#nav-pic{
    width: 5vw;
    border-radius: 50%;
    margin-right: 2vw;
}
.eachPost{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2vw;
}
#post{
    width: 60vw;
    background-color: white;
    border-radius: 4px;
    overflow: hidden;
}
#postHead{
    display: flex;
    background-color: #ed7966;
    align-items: center;
    justify-content: space-between;
}
.postHead{
    display: flex;
    align-items: center;
}
.postHead>img{
    width: 57px;
    border-radius: 50%;
    padding: 1vw;
}
.postHead>p{
    font-family: 'Staatliches', cursive,'Courier New', Courier, monospace;
    font-size: x-large;
    color: white;
}
.postBody>img{
    width: 58vw;
}
.postBody>p{
    font-family: 'Dancing Script', cursive, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: medium;
    text-align: left;
    padding-left: 1vw;

}
.dropDown{
    margin-right: 2vw;
    float: right;
    width: 20vw;
    position: absolute;
    right: 1px;
    background-color: #ed7966;
    border-radius: 4px;
    display: none;
}
.dropDown>p{
    color: white;
    font-family: 'Staatliches', cursive,'Courier New', Courier, monospace;
}
#deleteBtn{
    margin-right: 2vw;
    border-radius: 3px;
    border: none;
    color:#ed7966;
    padding: 1vw;
}
#photoUpload{
    display: none;
}
#photoLabel{
    background-color: #ed7966;
    padding: 1vw;
    border-radius: 3px;
    color: white;
}